<template>
  <div class="goods-info" v-if="Object.keys(detailInfo).length !== 0">
    <div class="info-desc clear-fix">
      <div class="start"></div>
      <div class="desc">{{ detailInfo.desc }}</div>
      <div class="end"></div>
    </div>
    <div class="info-key">{{ detailInfo.detailImage[0].key }}</div>
    <div class="info-list">
      <img
        :src="item"
        @load="imgLoad"
        alt=""
        v-for="(item, index) in detailInfo.detailImage[0].list"
        :key="index"
      />
    </div>
  </div>
</template>

<script>
export default {
  props: {
    detailInfo: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    return {
      counter: 0,
      imagesLength: 0
    };
  },
  methods: {
    imgLoad() {
      // 判断所有图片加载玩了，那么进行一次回调
      // 法一然后执行Detail中imageLoad方法中this.$refs.scroll.refresh();
      // if (++this.counter === this.imagesLength) {
      //   this.$emit("imageLoad");
      // }
      // 法二，然后执行Detail中imageLoad方法中this.newRefresh
      this.$emit("imageLoad");
    }
  },
  watch: {
    detailInfo() {
      //监听detailInfo属性的变化
      this.imagesLength = this.detailInfo.detailImage[0].list.length;
      // console.log(this.imagesLength);
    }
  }
};
</script>

<style scoped>
.goods-info {
  padding: 20px 2px;
  border-bottom: 5px solid #f2f5f8;
}
.info-desc {
  padding: 0 15px;
}

.info-desc .start,
.info-desc .end {
  width: 90px;
  height: 1px;
}
.info-desc .start {
  float: left;
}
.info-desc .end {
  float: right;
}
.info-desc .start::before,
.info-desc .end::after {
  content: "";
  position: absolute;
  width: 5px;
  height: 5px;
  background-color: #333;
  bottom: 0;
}
.info-desc .end::after {
  right: 0;
}
.info-desc .desc {
  padding: 15px 0;
  font-size: 14px;
}
.info-key {
  margin: 10px 0 10px 15px;
  color: #333;
  font-size: 15px;
}

.info-list img {
  width: 100%;
}
</style>
