<template>
  <div id="detail">
    <detail-nav-bar
      class="detail-nav"
      @titleClick="titleClick"
      ref="nav"
    ></detail-nav-bar>
    <scroll
      class="content"
      ref="scroll"
      @scroll="contentScroll"
      :probe-type="3"
    >
      <detail-swiper :top-images="topImages"></detail-swiper>
      <detail-base-info :goods="goods"></detail-base-info>
      <detail-shop-info :shop="shop"></detail-shop-info>
      <detail-goods-info
        :detail-info="detailInfo"
        @imageLoad="imageLoad"
      ></detail-goods-info>
      <detail-param-info
        :param-info="pramaInfo"
        ref="params"
      ></detail-param-info>
      <detail-comment-info
        :comment-info="commentInfo"
        ref="comment"
      ></detail-comment-info>
      <goods-list :goods="recommends" ref="recommend"></goods-list>
    </scroll>
    <detail-bottom-bar @addToCart="addToCart"></detail-bottom-bar>
    <!-- <back-top v-show="isShowBackTop" @click.native="backClick"></back-top> -->
    <back-top v-show="isShowBackTop" @click.native="backClick"></back-top>
    <!-- <toast :message="message" :show="show"/> -->
  </div>
</template>

<script>
import {
  getDetail,
  Goods,
  Shop,
  GoodsParam,
  getRecommend
} from "network/detail";
import DetailNavBar from "./childComps/DetailNavBar"; //最上面的导航条
import DetailSwiper from "./childComps/DetailSwiper"; //轮播图
import DetailBaseInfo from "./childComps/DetailBaseInfo"; //轮播图下面的标题，价格，
import DetailShopInfo from "./childComps/DetailShopInfo"; //商店信息
import DetailGoodsInfo from "./childComps/DetailGoodsInfo"; //货物详情信息(图片)
import DetailParamInfo from "./childComps/DetailParamInfo"; //参数（尺码，厂家，材料......）
import DetailCommentInfo from "./childComps/DetailCommentInfo"; //评论
import DetailBottomBar from "./childComps/DetailBottomBar"; //评论

import GoodsList from "components/content/goods/GoodsList";
import Scroll from "components/common/scroll/Scroll";
import BackTop from "components/content/backTop/BackTop";
// import Toast from 'components/common/toast/Toast.vue'

import { backTopMixin } from "common/mixin";
import { debounce } from "common/utils";
export default {
  name: "Detail",
  data() {
    return {
      iid: "",
      topImages: [],
      goods: {},
      shop: {},
      detailInfo: {},
      pramaInfo: {},
      commentInfo: {},
      recommends: [],
      themeTopYs: [],
      getThemeTopY: null,
      currentIndex: 0,
      message:'',
      show:false
      //   isShowBackTop:false
    };
  },
  mixins: [ backTopMixin],
  created() {
    // 1、保存传入的iid
    // console.log(this.iid);
    this.iid = this.$route.params.iid;

    //根据iid请求详情数据
    getDetail(this.iid).then(res => {
        console.log(res);
      const data = res.result;
      // 2、轮播图数据
      // console.log(this.topImages);
      this.topImages = data.itemInfo.topImages;

      // 3、获取商品信息
      this.goods = new Goods(
        data.itemInfo,
        data.columns,
        data.shopInfo.services
      );

      // 4、店铺信息
      this.shop = new Shop(data.shopInfo);
      //   console.log(this.goods);

      // 5、获取商品详细信息
      this.detailInfo = data.detailInfo;

      // 6、获取参数信息
      this.pramaInfo = new GoodsParam(
        data.itemParams.info,
        data.itemParams.rule
      );

      // 7、取出评论信息
      if (data.rate.cRate !== 0) {
        this.commentInfo = data.rate.list[0];
      }
      // this.$nextTick(()=>{
      // 值不对的原因:图片没有计算在类
      //     //根据最新的数据，对应的DOM是已经被演染出来
      //     //但是图片依然是没有加载完(目前获取到offsetTop不包含其中的图片).
      //     // offsetTop值不对的时候，都是因为图片的问题

      //     this.themeTopYs=[]
      //     this.themeTopYs.push(0)
      //     this.themeTopYs.push(this.$refs.params.$el.offsetTop)
      //     this.themeTopYs.push(this.$refs.comment.$el.offsetTop)
      //     this.themeTopYs.push(this.$refs.recommend.$el.offsetTop)
      //     console.log(this.themeTopYs);
      // })
    });

    // 请求推荐数据
    getRecommend().then(res => {
      this.recommends = res.data.list;
      // console.log(res.data.list);
    });

    // 给getThemeTopY赋值
    this.getThemeTopY = debounce(() => {
      this.themeTopYs = [];
      this.themeTopYs.push(0);
      this.themeTopYs.push(this.$refs.params.$el.offsetTop);
      this.themeTopYs.push(this.$refs.comment.$el.offsetTop);
      this.themeTopYs.push(this.$refs.recommend.$el.offsetTop);
      // console.log(this.themeTopYs);
    });
  },
  updated() {
    // this.themeTopYs=[]
    // this.themeTopYs.push(0)
    // this.themeTopYs.push(this.$refs.params.$el.offsetTop)
    // this.themeTopYs.push(this.$refs.comment.$el.offsetTop)
    // this.themeTopYs.push(this.$refs.recommend.$el.offsetTop)
    // console.log(this.themeTopYs);
  },
  components: {
    DetailNavBar,
    DetailSwiper,
    DetailBaseInfo,
    DetailShopInfo,
    Scroll,
    DetailGoodsInfo,
    DetailParamInfo,
    DetailCommentInfo,
    GoodsList,
    DetailBottomBar,
    // Toast
    // BackTop
  },
  methods: {
    imageLoad() {
      this.newRefresh;
      //   this.$refs.scroll.refresh();

      this.getThemeTopY();
    },
    titleClick(index) {
      this.$refs.scroll.scrollTo(0, -this.themeTopYs[index] + 44, 200);
    },
    contentScroll(position) {
      const positionY = -position.y;
      let length = this.themeTopYs.length;
      // console.log(positionY);
      for (let i = 0; i < length; i++) {
        if (
          this.currentIndex !== i &&
          ((i < length - 1 &&
            positionY >= this.themeTopYs[i] &&
            positionY < this.themeTopYs[i + 1]) ||
            (i === length - 1 && positionY >= this.themeTopYs[i]))
        ) {
          this.currentIndex = i;
          this.$refs.nav.currentIndex = this.currentIndex;
        }
      }

      this.listenShowBackTop(position);
    },
    addToCart(){
        // 1、获取购物车需要的展示的信息
        const product={}
        product.image = this.topImages[0]
        product.title=this.goods.title
        product.desc=this.goods.desc
        product.price=this.goods.realPrice
        product.iid=this.iid
        console.log(this.goods.realPrice);
        // 2、将商品添加到购物车 
        // this.$store.commit("addCart",product)放在mutitions中的用法
        this.$store.dispatch("addCart",product).then(res=>{
            //   this.message=res;
            //   this.show=true;
            // setTimeout(()=>{
            //   this.message="";
            //   this.show=false;
            // },1500)

            this.$toast.show(res,2000)
        })
        
    }

    // backClick() {
    //   this.$refs.scroll.scrollTo(0, 0, 500);
    // },
  },
  mounted() {
    const refresh = debounce(this.$refs.scroll.refresh, 100);
    this.$bus.$on("detailItemImageLoad", () => {
      refresh();
    });
  }
};
</script>

<style scoped>
#detail {
  height: 100vh;
  position: relative;
  z-index: 9;
  background-color: #fff;
}
/* .detail-nav {
  position: relative;
  z-index: 9;
  background-color: #fff;
} 法一*/
.content {
  height: calc(100% - 44px - 49px);
  overflow: hidden;
  /* 法二 */
}
</style>
